import axios from 'axios';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import Badge from '../Badge/Badge';
import CardSection from '../CardSection/CardSection';
import ImageContainer from '../ImageContainer/ImageContainer';
import Spinner from '../Spinner/Spinner';
import Title from '../Title/Title';
import 'swiper/swiper.scss';

export default {
  components: {
    Swiper,
    SwiperSlide,
    tripBadge: Badge,
    tripCardSection: CardSection,
    tripImageContainer: ImageContainer,
    tripSpinner: Spinner,
    tripTitle: Title,
  },
  props: {
    cityName: String,
    location: {
      lat: Number,
      lng: Number,
    },
  },
  data() {
    return {
      isLoading: false,
      placesData: null,
      swiperOption: {
        spaceBetween: 10,
        slidesPerView: 'auto',
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
    };
  },
  watch: {
    location: async function(newVal, oldVal) {
      if (oldVal.lat !== newVal.lat) {
        const { lat, lng } = newVal;
        this.isLoading = true;
        const res = await axios.get(`/yelp?lat=${lat}&lng=${lng}`);
        if (res) this.isLoading = false;
        this.placesData = res.data;
      }
    },
  },
};
